.SectionContainer {
  background-repeat: repeat;
  background-size: contain;
  min-height: calc(90vh - 100px);
  background-color: #fbfbfb;
  border-radius: 0 40px 40px 40px; }
  .SectionContainer .CardComment {
    font-size: 20px;
    color: #444444; }

.SectionCall {
  font-weight: 600;
  font-size: 35px;
  line-height: 45px; }

.BtnCardCadeau {
  position: relative;
  font-family: inherit;
  font-weight: 700;
  cursor: pointer;
  white-space: nowrap;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 12px 0px;
  min-height: 48px;
  background-color: #e02167;
  color: white;
  transition: background-color 0.2s ease 0s, color 0.2s ease 0s, border-color 0.2s ease 0s, opacity 0.2s ease 0s;
  outline: none;
  border-width: 1px;
  border-style: solid;
  border-image: initial;
  margin: 0px;
  padding: 8px 18px;
  border-color: transparent;
  border-radius: 4px;
  font-size: 20px; }
  .BtnCardCadeau:hover {
    color: white !important;
    background-color: #e5427e; }
